.gpt3__header {
    display: flex;
    
    
}

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;

}

.gpt3__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: #052D56;
    font-family: var(--font-family);
    color: #3D6184;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
  
    border-radius: 5px 0px 0px 5px;
    border: none;
    outline: none;
    padding: 0 1rem;

}

.gpt3__header-content__input button {
    flex: 0.6;
    background: #FF4820;
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27.32px;
    font-weight: 700;
    width: 100%;
    min-height: 50px;
    border-radius: 0px 5px 5px 0px;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0 1rem;
}

.gpt3__header-content__people {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-top: 2rem;
}

.gpt3__header-content__people p {
    color: #fff;
    font-weight: 500;
    font-size: 12px;
    line-height: 37.61px;
    margin: 0 0 0 1rem;
}

.gpt3__header-content__people img {
    height: 38px;
    width: 181.79px;
    display: flex;
}

.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width : 1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}


@media screen and (max-width : 650px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header h1 {
        font-size: 45px;
        line-height: 60px;
       
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people  {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;
    }
    .gpt3__header-content__people p{
        margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
       font-size: 15px;
       line-height: 24px;
    }
    
}
@media screen and (max-width : 495px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height: 48px;
       
    }
    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3__header-content__people  {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;
    }
    .gpt3__header-content__people p{
        margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
       font-size: 11px;
       line-height: 16px;
    }
   
}